.getapp{
	width:100%;
	height:300px;
	background:#ff6000;
	display:flex;
	flex-direction:column;
	flex-wrap:wrap;
}
.phone{
	width:50%;
	height:280px;
	box-sizing:border-box;
}
.phone img{
	height:430px;
	margin-top:-50px;
	margin-left:80px;
}
.Getapp{
	width:40%;
	height:280px;
	margin-left:-80px;
	color:white;
}
.Getapp button{
	padding:10px;
	width:150px;
	border-radius:5px;
	border:1px solid white;
	color:#ff6000;
	cursor:pointer;
}
.Getapp button:hover{
	cursor:pointer;
}
@media (max-width:690px){
	.phone img{
	height:380px;
	margin-top:-30px;
}

}
@media (max-width:500px){

.getapp{
	width:100%;
	height:auto;
	background:#ff6000;
	display:flex;
	flex-direction:column;
	align-items:center;
	justify-content:flex-start;
}
.phone img{
	margin-left:0px;
}
.Getapp{
	margin-top:-40px;
	margin-bottom:50px;
}
}
@media (max-width:550px){
	.getapp{
		height:320px;
	}
	.Getapp{
	margin-bottom:50px;
}
.phone img{
	margin-left:0px;
}
}
@media (max-width:400px){
	.getapp{
		height:320px;
		align-items:center;
	}
	.Getapp{
	margin-bottom:50px;
	margin-left:-160px;
	width:200px;

}
.phone img{
	margin-left:0px;
}
}
