.restodetail{
width:100%;
min-height:600px;
height:auto;
margin-top:9px;
background:white;
}
.restoheader{
	width:100%;
	height:350px;
	margin-top:30px;
	box-sizing: border-box;
	background-size: cover;
	background-image:url("./assets/resto_bg.png");
}
.forheaderthumb{
	width:50%;
	height:350px;
	height:100px;
	display:flex;
	flex-direction:row;
	align-items:center;
}
.forheaderthumb section{
margin-left:60px;
color:white;
}
.forheaderthumb section img{
	margin-left:60px;
}
.forheaderthumb section h3,h5{
	margin-left:-40px;
}
.forheaderthumb section h5{
	margin-left:-140px;
}
.restocontents{
	width:100%;
	height:auto;
	margin-top:-16px;
	padding:10px;
	box-sizing: border-box;
	background:#f5fafc;
	display:grid;
	grid-template-columns:30% 45%;
	grid-template-rows:auto;	
	grid-gap:10%;
}

.restocontents #forrateandinfo,#formenu{
background:white;
margin-top:50px;
box-sizing: border-box;
margin-bottom:50px;
box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
}
.linkscontents{
	width:100%;
	height:auto;
	box-shadow:0px 0px 1px 0px rgba(0,0,0,0.19);
	background:white;
	margin-top:10px;
}
#formenu{
	height:200px;
}
#forrateandinfo{
	margin-left:3em;
	min-height:450px;
	display:flex;
   flex-direction:column;
}
.forlink{
	width:100%;
	height:50px;
	box-shadow:0px 0px 3px 0px  rgba(0,0,0,0.19);
}
.forlink ul{
	display:flex;
	flex-direction:row;
	justify-content:flex-start;
	list-style:none;
	line-height:20px;
}
.forlink ul li{
	margin-left:20px;
	height:50px;

}
.forlink ul li:hover{
	color:#ff6000;
	cursor: pointer;
}
.Link{
	text-decoration:none;
	color:black;
}
.foractive{
	color:#ff6000;
	border-bottom:5px solid #ff6000;
}
#forInfo{
	width:100%;
	box-sizing:border-box;
	padding:5px;
	height:400px;
	background:transparent;
	margin-bottom:10px;
	display:flex;
	flex-direction:row;
	align-items:center;
	justify-content:flex-start;
}
#forInfo #forlabel{
	background:white;
	height:390px;
	width:30%;
	box-sizing: border-box;
}
#forInfo #forlabel p{
font-size:19px;
font-family: "BlinkMacSystemFont"
}

#forInfo #forlabelcontent{
	background:white;
	height:390px;
	width:70%;

}
#forInfo #forlabelcontent p{
font-size:19px;
font-family: "BlinkMacSystemFont"
}


.forRating{
height:auto;
min-height:400px;
}
.forRating ul{
	display:flex;
	flex-direction:column;
	justify-content:space-around;
}
.forRating ul li{
list-style-type:circle;
list-style:none;
width:30px;
height:30px;
margin-left:10px;
margin-top:60px;
background:black;
border-radius:50px;
border:1px solid black;
cursor: pointer;
}
.forRating ul li h4,h5{
	margin-left:50px;
	margin-top:-2px;
}

.forRating ul li h4{
	font-size:22px;
	font-family:"Times New Roman"
}
.forRating ul li h5{
	color:#ff6000;
	font-size:12px;
	width:100px;
	margin-left:19px;
	margin-top:-18px;
}
.forRating ul li p{
	width:300px;
	padding:2px;
	margin-top:;
}
.formenutitle{
	color:#ff6000;
width:100px;
height:30px;
padding:5px;
margin-left:20px;
margin-top:-2px;
}
.formenutitle p{
	font-size:22px;
	font-family:"Times New Romans";
	font-weight:105px;
}
.highlights{
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	height:300px;

}
.highlight label{
	letter-spacing:5px;
	line-height:10px;
}
.fortoggle{
	height:30px;
	width:100px;
	border-bottom:5px solid #ff6000;
	color:#ff6000;
	transition: 0.2s ease-out;
	cursor:pointer;
}
@media (max-width:950px){

.restocontents{
	display:grid;
	grid-template-rows:50% 50%;
	grid-template-columns:auto;	
	grid-gap:10%;
}
#formenu{
	height:200px;
}
#forrateandinfo{
	min-height:450px;
	margin-left:-2px;
}
}